.header{
  background-color: #fff;
  border: 1px solid rgb(223, 219, 219);
  display: grid;
  grid-template-columns: 1fr;
  position:fixed;
  top:0;
  z-index: +2;
}
.webHeader{
  display: grid;
  grid-template-columns: 8fr 2fr 0.3fr;

  .actions{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    p:hover{
        color:greenyellow;
        cursor: pointer;
    }
  }
}
.mobileHeader{display: none;}

@media (max-width:600px){
.webHeader{display: none;}
.mobileHeader{
  width: 105vw;
  padding: 2px 29px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  div{
    width: 50%;
  }
  .mobNav{
    text-align: right;
  }
}
}